<template>
    <div class="BagOrder">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="money">
        <p class="cl_666 mb20">应付金额(元)</p>
        <p class="f46 cl_f90">$100.00</p>
        <img src="../../../assets/img/common/succeed_f90.png" alt="" >
        <p class="cl_999 mb24">请打开手机支付宝，扫一扫完成支付</p>
        <p class="countDown">二维码有效时长：
          <van-count-down :time="time" class="dib"   >
            <template #default="timeData">
              <span class="block">{{ timeData.minutes}}</span>
              <span class="colon">:</span>
              <span class="block">{{ timeData.seconds }}</span>
            </template>
          </van-count-down>
        </p>
      </div>
      <div class="fiexoverTitle mt24">
        <div class="fiexover  border-bD8D8D8">
          <p>总价</p>
          <div class="f34 cl_333 h_100 lh102">
            ￥123.56
          </div>
        </div>
      </div>
      <div class="fiexoverTitle">
        <div class="fiexover">
          <p>总价</p>
          <div class="f34 cl_333 h_100 lh102">
            -￥123.56
          </div>
        </div>
      </div>
      <div class="public-btn mt60  mr30 ml30" @click="jump">
        保存到手机
      </div>
    </div>
</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "BagOrder",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: {name: "支付订单",left: true},
              // timeData:{ "days": 0, "hours": 0, "minutes": 2, "seconds": 0, "milliseconds": 987 },
              time: 1 * 2 * 60 * 1000,
            }
        },
        methods: {
          //跳转到支付状态
          jump(){
            this.$router.push({
              name:"PayOver",
              params:{id:1},
            })

          },
        },
        created() {

        }
    }

</script>

<style scoped>
  .BagOrder .money{width: 6.9rem;height: 5.68rem;margin:0 auto;margin-top: .24rem;background: #fff;padding-top: .6rem;font-size: .28rem;text-align: center}
  .BagOrder .money img{width: 1.8rem;height: 1.8rem;display: inline-block;margin-bottom: .32rem;}

</style>



